
.header-container {
  padding: 0 20px;
  display: flex;
  justify-content: space-between; // 水平两端对齐
  align-items: center; // 垂直居中对齐
  height: 60px;
  background-color: #333;

  .bread-text {
    color: white;
    font-size: 14px;
    margin: 10px;
  }
  .r-content {
    .user {
      width: 40px;
      height: 40px;
      border-radius: 50%;
    }
  }
  .l-content {
    display: flex;
    align-items: center;
    color: #fff;
    .el-button {
      margin-right: 20px;
    }
    // 这些样式是通过在浏览器处检查得到的
    ::v-deep .el-breadcrumb {
      .el-breadcrumb__item {
        .el-breadcrumb__inner {
          font-weight: 400;
          &.is-link {
            color: #666;
          }
        }
        // ？last-child
        &:last-child {
          .el-breadcrumb__inner {
            &.is-link {
              color: #fff;
            }
          }
        }
      }
    }
  }
}
